.App {
  background-color: brown;
  position:absolute;
  top:0px;
  right:0px;
  bottom:0px;
  left:0px;
}


.header{
  background-color: brown;
  text-align: left;
}

.input1{
  border: 1px solid black;
  margin-top: 50px;
  padding-top: 10px;
  padding-bottom: 30px;
  text-align: center;
  width: 75%;
  box-shadow: 5px 5px;
  background: darkcyan;
}

.title{
  text-align: center;
  display: flex;
  justify-content: center;
  margin-top: 30px;

}
.titleDiv{
  width: 50%;
  
  background-color: lightblue;
}

.background{
  background: lightblue;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
}

.res{
  display: flex;
