  text-align: center;
  box-shadow: 5px 5px;
  width: 75%;
  padding-bottom: 90px;
  background: darkcyan;
}

.second {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
}

.button1{
  margin-top: 20px;
}


.resButton{
  text-align: center;
